.app_wrapper{
  display: grid;
  padding: 25px;
  width: 100 px;
  margin: 3px;
    grid-template-areas: 
    ". h h ."
    ". n c ."
  ;
  grid-template-columns: 1fr 2fr 5fr 1fr ;
  grid-template-rows: 150px auto;
  grid-gap: 5px;
}
.app_wrapper_content{
  background-color: darkkhaki;
  grid-area: c; 
}
 
.myheader{
  grid-area: h;
  background-color: chartreuse;
}


